import React, { Component } from "react";
import AreaProvinsiKota from "./Component/AreaProvinsiKota";
import Table from "./Component/Table";
import { connect } from "react-redux";
import { editLokasi, getLokasi } from "../actions/lokasiAction";
import notrespond from "../Images/500.svg";
import activelogo from "../Images/active.png";
import nonactive from "../Images/nonactive.png";
import axios from "axios";
import Swal from "sweetalert2";
import FormLokasiEdit from "./Component/FormLokasiEdit";
import { reset } from "redux-form";
import Skeleton from "react-loading-skeleton";

const server = process.env.REACT_APP_BACKEND_URL;
function status(cell, row) {
  if (row.statusAktif === "AKTIF") {
    return (
      <div>
        <img src={activelogo} style={{ width: 50, height: 50 }} alt="ACTIVE" />
      </div>
    );
  } else {
    return (
      <div>
        <img
          src={nonactive}
          alt="NON ACTIVE"
          style={{ width: 50, height: 50 }}
        />
      </div>
    );
  }
}

const defaultSorted = [
  {
    dataField: "kodeLokasi",
    order: "asc",
  },
];
function hapus(dispatch, kode) {
  Swal.fire({
    title: "Anda Yakin ?",
    text: "Anda Akan Menghapus Data Ini ?",
    icon: "warning",
    showCancelButton: true,
    confirmButtonColor: "#3085d6",
    cancelButtonColor: "#d33",
    confirmButtonText: "Yes, delete it!",
  }).then((result) => {
    if (result.isConfirmed) {
      axios
        .delete(server + "storage", {
          data: {
            kodeLokasi: kode,
          },
        })
        .then((res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Dihapus!",
            showConfirmButton: false,
            timer: 1500,
          })
        )
        .catch((err) =>
          Swal.fire({
            position: "top-end",
            icon: "error",
            text: "Data Gagal Dihapus!",
            showConfirmButton: false,
            timer: 1500,
          })
        )
        .then(() => dispatch(getLokasi()));
    }
  });
}

const maptoState = (state) => {
  return {
    lokasi: state.provinsi.lokasi,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
  };
};
class Lokasi extends Component {
  constructor(props) {
    super(props);
    this.state = {
      Lokasi: [],
      response: "",
      columns: [
        {
          dataField: "kodeLokasi",
          text: "Kode Lokasi",
          sort: true,
          headerStyle: () => {
            return { width: "15%" };
          },
        },
        {
          dataField: "namaLokasi",
          text: "Nama Lokasi",
          sort: true,
        },
        {
          dataField: "statusAktif",
          text: "Status",
          sort: true,
          formatter: status,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "link",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-danger mr-2"
                  onClick={() => hapus(this.props.dispatch, row.kodeLokasi)}
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
                <button
                  data-toggle="modal"
                  data-target="#editModal"
                  className="btn btn-warning"
                  onClick={() =>
                    this.props.dispatch(
                      editLokasi(
                        row.tipeData,
                        row.kodeLokasi,
                        row.namaLokasi,
                        row.status,
                        row.kodeExt
                      )
                    )
                  }
                >
                  <i className="fa fa-edit mr-2"></i>
                  EDIT
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getLokasi());
  }

  handleSubmit(data) {
    const isi = {
      statusAktif: data.status ? data.status : "AKTIF",
      tipeData: "LOKASI",
      kodeLokasi: data.KodeLokasi,
      namaLokasi: data.NamaLokasi,
      kodeExt: data.kodeExt,
    };
    console.log(isi);
    axios
      .post(server + "storage", isi)
      .then(
        (res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Disimpan!",
            showConfirmButton: false,
            timer: 1500,
          }),
        document.getElementById("dismiss_modal").click()
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Data Gagal Disimpan!",
          showConfirmButton: false,
          timer: 1500,
        })
      )
      .then(() => this.props.dispatch(getLokasi()))
      .then(() => this.props.dispatch(reset("Provinsi")));
  }
  handleEdit(data) {
    const isi = {
      statusAktif: data.status ? data.status : "AKTIF",
      tipeData: "LOKASI",
      kodeLokasi: data.KodeLokasi,
      namaLokasi: data.NamaLokasi,
      kodeExt: data.kodeExt,
    };
    console.log(isi);
    axios
      .put(server + "storage", isi)
      .then(
        (res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Dirubah!",
            showConfirmButton: false,
            timer: 1500,
          }),
        document.getElementById("dismiss_modal1").click()
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Data Gagal Dirubah!",
          showConfirmButton: false,
          timer: 1500,
        })
      )
      .then(() => this.props.dispatch(getLokasi()));
  }

  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div id="notif_alert" className={this.props.alert} role="alert">
          <button
            type="button"
            className="close"
            data-dismiss="alert"
            aria-label="Close"
          >
            <span aria-hidden="true">×</span>
          </button>
          <strong>Error - </strong> Tidak Bisa Mengambil Data
        </div>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <h4 className="card-title mb-3" style={{ fontSize: 30 }}>
                  Data Lokasi
                </h4>
                <div className="col-lg-12 col-md-12">
                  <div className="text-right">
                    <button
                      name="simpan"
                      id="simpan"
                      type="button"
                      className="btn btn-info"
                      data-toggle="modal"
                      data-target="#primary-header-modal"
                    >
                      Tambah Data Lokasi
                    </button>
                  </div>
                </div>
                {this.props.lokasi ? (
                  <Table
                    data={this.props.lokasi}
                    columns={this.state.columns}
                    defaultSorted={defaultSorted}
                    namaKey="kodeLokasi"
                  />
                ) : (
                  <div>
                    {this.props.error ? (
                      <div className="text-center">
                        <img
                          src={notrespond}
                          style={{ width: "30%" }}
                          alt="NOT RESPOND"
                        />
                        <br />
                        <h4>Request API Gagal, Silahkan Refresh</h4>
                      </div>
                    ) : (
                      <Skeleton width={"100%"} height={180} />
                    )}
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Lokasi
                </h4>
                <button
                  type="button"
                  id="dismiss_modal"
                  name="dismiss_modal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <AreaProvinsiKota
                  nama="Nama Lokasi"
                  kode="Kode Lokasi"
                  from="lokasi"
                  onSubmit={(data) => this.handleSubmit(data)}
                  kode_ext="Kode Ext"
                />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
        <div
          id="editModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Edit Data Lokasi
                </h4>
                <button
                  type="button"
                  id="dismiss_modal1"
                  name="dismiss_modal1"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <FormLokasiEdit
                  nama="Nama Lokasi"
                  kode="Kode Lokasi"
                  from="lokasi"
                  onSubmit={(data) => this.handleEdit(data)}
                  kode_ext="Kode Ext"
                />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoState, null)(Lokasi);
