import React, { Component } from "react";
import axios from "axios";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { validationLokasi } from "../../Validations/validationLokasi";

const server = process.env.REACT_APP_BACKEND_URL;
const renderField = ({
  input,
  label,
  type,
  readOnly,
  limit,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      readOnly={readOnly}
      maxLength={limit}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);

const maptoState = (state) => {
  return {
    initialValues: {
      status: state.provinsi.editLokasi.Status,
      tipeData: state.provinsi.editLokasi.tipeData,
      NamaLokasi: state.provinsi.editLokasi.namaLokasi,
      KodeLokasi: state.provinsi.editLokasi.kodeLokasi,
      kodeExt: state.provinsi.editLokasi.kodeExt,
    },
  };
};
class FormLokasiEdit extends Component {
  constructor(props) {
    super(props);
    this.state = {
      response: "",
      status: "AKTIF",
      listProvinsi: [],
    };
  }

  componentDidMount() {
    axios
      .get(server + "citys")
      .then((res) =>
        this.setState({
          listProvinsi: res.data,
        })
      )
      .catch((err) => console.log(err));
  }

  render() {
    var kodenya = this.props.kode;
    var namanya = this.props.nama;
    return (
      <div>
        <form onSubmit={this.props.handleSubmit}>
          <div className="row">
            <div className="col-md-2">
              <Field
                name={kodenya.replace(/\s/g, "")}
                type="text"
                normalize={(value) => value && value.toUpperCase()}
                component={renderField}
                label={this.props.kode}
                readOnly={true}
                limit={5}
              />
            </div>
            <div className="col-md-4">
              <Field
                name="kodeExt"
                type="text"
                normalize={(value) => value && value.toUpperCase()}
                component={renderField}
                label={this.props.kode_ext}
              />
            </div>
            <div className="col-md-4">
              <Field
                name={namanya.replace(/\s/g, "")}
                type="text"
                normalize={(value) => value && value.toUpperCase()}
                component={renderField}
                label={this.props.nama}
              />
            </div>
            <div className="col-md-2">
              <div className="form-group">
                <label htmlFor="" className="text-black">
                  Status
                </label>
                <Field
                  component="select"
                  name="status"
                  type="text"
                  normalize={(value) => value && value.toUpperCase()}
                  className="form-control"
                >
                  <option value="AKTIF" key="AKTIF">
                    Aktif
                  </option>
                  <option value="NON-AKTIF" key="NON-AKTIF">
                    Non Aktif
                  </option>
                </Field>
              </div>
            </div>
            <div className="col-lg-12 col-md-12">
              <div className="text-right">
                <button
                  type="submit"
                  id="simpan"
                  className="btn btn-primary"
                  disabled={this.props.submitting}
                >
                  Submit
                </button>
              </div>
            </div>
          </div>
        </form>
      </div>
    );
  }
}

FormLokasiEdit = reduxForm({
  form: "LokasiEdit", // a unique identifier for this form
  validate: validationLokasi, // <--- validation function given to redux-form
  enableReinitialize: true,
})(FormLokasiEdit);
export default connect(maptoState, null)(FormLokasiEdit);
